export const isBrowser = typeof window !== 'undefined'

export const copyToClipBoard = (text: string) => {
  isBrowser &&
    window.navigator.clipboard
      .writeText(text)
      .then(() => {
        alert(`‚úÖ : ${text}`)
      })
      .catch(() => {
        alert('‚ùå Îã§Ïãú ÏãúÎèÑÌï¥Ï£ºÏÑ∏Ïöî.')
      })
}
